.accordionHeader div:not(.collapsed),
.accordionHeader button:not(.collapsed) {
	background-color: var(--color-secondary) !important;
}

.accordionHeader {
	border-bottom: 1px solid var(--color-tertiary) !important;
}

.accordionHeader > div {
	border-top-right-radius: 0.375rem !important;
	border-top-left-radius: 0.375rem !important;
}

.accordionHeader button:not(.collapsed) {
	box-shadow: none !important;
}

.accordionItem {
	border: 1px solid var(--color-tertiary) !important;
	background-color: var(--color-secondary) !important;
}

.accordionItem:not(:first-child) {
	border-top: none !important;
}

.accordionButtonIcon::after {
	flex-shrink: 0;
	margin-left: auto;
	content: '\f078' !important;
	font-family: 'Font Awesome 5 Free';
	font-size: 1rem;
	color: var(--color-header);
	transition: var(--transition-default);
	background-image: none !important;
}
