
.inputField {
	color: var(--color-body) !important;
	border-color: var(--color-tertiary);
	box-shadow: var(--inner-shadow);
}

.inputField:focus {
	box-shadow: none;
	border-color: var(--color-header);
}

.label {
	color: var(--color-header) !important;
}

.label::after {
	background-color: transparent !important;
}